@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  padding: 0;
  margin: 0;
}
body{
  font-family: 'Montserrat',sans-serif;
  color: #888;
  padding: 0;
  margin: 0;
}
.link {
  color: #fa0372;
}
.padding{
  padding: 1rem;
}
body *{
  margin: 0;
}
.hero {
  margin: 40px auto;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 50px;
}
.left{
  height: 250px;
  width: 250px;
  background: #fed94d;
  border-radius: 50%;
  display: inline-flex;
  -webkit-transform: translateX(30%) translateY(10%);
          transform: translateX(30%) translateY(10%);
  z-index: 1;
  box-shadow: 0 10px 20px rgba(0,0,0,0.2);
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 120px;
  -webkit-animation: slide-left 0.6s ease-out forwards;
          animation: slide-left 0.6s ease-out forwards;
}
@-webkit-keyframes slide-left {
  from {
      -webkit-transform:  translateX(30%) translateY(20%) translateX(-40%);
              transform:  translateX(30%) translateY(20%) translateX(-40%);
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform:  translateX(30%) translateY(10%) translateX(000%);
              transform:  translateX(30%) translateY(10%) translateX(000%);
  }
}
@keyframes slide-left {
  from {
      -webkit-transform:  translateX(30%) translateY(20%) translateX(-40%);
              transform:  translateX(30%) translateY(20%) translateX(-40%);
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform:  translateX(30%) translateY(10%) translateX(000%);
              transform:  translateX(30%) translateY(10%) translateX(000%);
  }
}
.middle{
  height: 400px;
  width: 400px;
  background: #005ed1ab;
  border-radius: 50%;
  font-size: 5rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 15rem;
  color: #fff;
  /* box-shadow: 0 10px 20px rgba(0,0,0,0.2); */
  -webkit-animation: logo-spin 10s linear infinite;
          animation: logo-spin 10s linear infinite;
}
@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes slide-down {
  from {
      -webkit-transform: translateY(10%);
              transform: translateY(10%);
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform: translateY(000%);
              transform: translateY(000%);
  }
}
@keyframes slide-down {
  from {
      -webkit-transform: translateY(10%);
              transform: translateY(10%);
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform: translateY(000%);
              transform: translateY(000%);
  }
}
.right{
  display: inline-flex;
  height: 250px;
  width: 250px;
  justify-content: center;
  align-items: center;
  background: #fa0372;
  border-radius: 50%;
  -webkit-transform: translateX(-30%) translateY(10%);
          transform: translateX(-30%) translateY(10%);
  z-index: 1;
  box-shadow: 0 10px 20px rgba(0,0,0,0.2);
  font-size: 10rem;
  color: #fff;
  -webkit-animation: slide-right 0.5s ease-out forwards;
          animation: slide-right 0.5s ease-out forwards;
}
@-webkit-keyframes slide-right {
  from {
      -webkit-transform: translateX(00%) translateY(20%) ;
              transform: translateX(00%) translateY(20%) ;
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform:translateX(-30%) translateY(10%) ;
              transform:translateX(-30%) translateY(10%) ;
  }
}
@keyframes slide-right {
  from {
      -webkit-transform: translateX(00%) translateY(20%) ;
              transform: translateX(00%) translateY(20%) ;
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform:translateX(-30%) translateY(10%) ;
              transform:translateX(-30%) translateY(10%) ;
  }
}
.main-heading {
  font-size: 50px;
  color: #aaa;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  position: relative;
  -webkit-animation: visible 1s ease-in-out forwards;
          animation: visible 1s ease-in-out forwards;
}
@-webkit-keyframes visible {
  from {
      -webkit-transform: translateY(20%);
              transform: translateY(20%);
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform: translateY(00%);
              transform: translateY(00%);
  }
}
@keyframes visible {
  from {
      -webkit-transform: translateY(20%);
              transform: translateY(20%);
      opacity: 0;
  }
  to {
      opacity: 1;
      -webkit-transform: translateY(00%);
              transform: translateY(00%);
  }
}
.main-heading > h1 {
  padding: 2rem;
  /* box-shadow: 0 0 40px rgba(0,0,0,0.1); */
  border-radius: 30px;
  color: #ccc;
  font-weight: lighter;
  -webkit-text-fill-color: transparent;
  background: -webkit-linear-gradient(transparent, transparent),
              url(https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2018/05/Gradient-Roundup-Illustrator-02.jpg) repeat;
  background: -o-linear-gradient(transparent, transparent);
  -webkit-background-clip: text;
  background-size: cover;
  margin: 0;
}
.colored-text{
  -webkit-text-fill-color: transparent;
  background: -webkit-linear-gradient(transparent, transparent),
              url(https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2018/05/Gradient-Roundup-Illustrator-02.jpg) repeat;
  background: -o-linear-gradient(transparent, transparent);
  -webkit-background-clip: text;
  background-size: cover;
}
body nav {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #aaa;
  padding: 30px 5%;
}
body nav a {
  text-decoration: none;
  color: #aaa;
  font-size: 40px;
  margin: 20px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: white;
  box-shadow: 0 0 30px rgba(0,0,0,0.1);
  background: white;
}
body nav a:nth-child(2) {
  text-decoration: none;
  color: #aaa;
  font-size: 16px;
  margin: 20px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  border-radius: 60px;
  background: white;
  padding: 0 2rem;
  height: 60px;
  white-space: nowrap;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: white;
  box-shadow: 0 0 30px rgba(0,0,0,0.1);
}
body nav a:nth-child(2):hover {
  text-decoration: none;
  color: #ff6060;
  font-size: 16px;
  margin: 20px;
}
body nav a:first-child::before {
  content: 'View Github repository';
  position: absolute;
  left: 0;
  top: 0;
  width: 320px;
  height: 60px;
  border-radius: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  color: #aaa;
  font-size: 16px;
  opacity: 0;
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
body nav a:last-child::before {
  content: 'View on NPM';
  position: absolute;
  right: 0;
  top: 0;
  width: 250px;
  height: 60px;
  border-radius: 60px;
  display: flex;
  color: #aaa;
  justify-content: center;
  align-items: center;
  z-index: -1;
  font-size: 16px;
  opacity: 0;
  -webkit-transform: translateX(10%);
          transform: translateX(10%);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
body nav a:hover {
  text-decoration: none;
  color: #ff6060;
  font-size: 30px;
  margin: 20px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
body nav a:hover::before {
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}
.main-heading > div{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.desc {
  max-width: 80vw;
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
  line-height: 1.5;
}
.container{
  max-width: 768px;
  margin: 0 auto;
}
.usage{
  padding: 50px 0 0;
  text-align: center;
}
.btn-toggle {
  background: #fa0373;
    border: none;
    border-radius: 10px;
    height: 80px;
    padding: 0 4rem;
    color: #fff;
    font-size: 2rem;
    cursor: pointer;
    -webkit-transition: all 0.10s;
    transition: all 0.10s; 
}
.btn-toggle:active {
  -webkit-transform: scale(0.90);
          transform: scale(0.90);
}
.btn-toggle:focus{
  outline: none;
  box-shadow: none;
}
.code{
  border: 1px solid #ddd;
  padding: 1rem;
  border-radius: 10px;
  text-align: left;
  margin: 20px auto;
  color: #444;
  background: #fafafa;
  letter-spacing: 1px;
  word-break: break-all;
  font-weight: bold;
  color: #787878;
}
.comment {
  margin: 0 auto;
  max-width: 500px;
}
.comment ul{
  text-align: left;
}
.comment ul li{
  color: #888;
  margin: 10px 0;
}
h3{
  font-weight: normal;
}
.highlight{
  color:#fa0373;
  letter-spacing: 0;
}
@media only screen and (max-width: 500px) {

  .main-heading {
      font-size: 20px;
      color: #aaa;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      position: relative;
  }
  .hero {
      display: block;
      margin: 0 auto;
      padding: 10px;
  }
  .hero .left{
      display: none;
  }
  .hero .right{
      display: none;
  }
  .hero .middle{
      width: 100%;
  }
  body nav a{
      margin: 0;
  }
  body nav a:hover {
      -webkit-transform: scale(1) translate(0);
              transform: scale(1) translate(0);
      margin: 0;
  }
  body nav a:hover:before{
      -webkit-transform: scale(0) translate(0);
              transform: scale(0) translate(0);
      opacity: 0;
  }
}
.footer{
  height: 60px;
  background: #fa0373;
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}
.footer a {
  text-decoration: none;
  color: #fff;
}
